import { ALLOWED_DOMAINS, deviceClasses } from './properties';
export function translateEntityState(hass, state, domain) {
    const localized = hass.localize(`component.${domain}.entity_component._.state.${state}`);
    return localized || state;
}
export function computeLabelCallback(hass, schema, domain, deviceClass) {
    switch (schema.name) {
        case "header": {
            if (domain && deviceClass) {
                return `${hass.localize("ui.panel.lovelace.editor.card.entities.name")} in ${hass.localize(`ui.dialogs.entity_registry.editor.device_classes.${domain}.${deviceClass}`)}`;
            }
            else if (domain) {
                return `${hass.localize("ui.panel.lovelace.editor.card.entities.name")} in ${hass.localize(`component.${domain}.entity_component._.name`)}`;
            }
            return hass.localize("ui.panel.lovelace.editor.card.entities.name");
        }
        case "status": {
            return hass.localize("ui.components.selectors.selector.types.state");
        }
        case "state": {
            if (domain) {
                return translateEntityState(hass, schema.name, domain);
            }
        }
        case "add":
            return hass.localize("ui.common.add");
        case "close":
            return hass.localize("ui.common.close");
        case "remove":
            return hass.localize("ui.panel.lovelace.editor.entities.remove");
        case "disabled":
            return hass.localize("ui.dialogs.entity_registry.editor.disabled_label");
        case "show_more":
            return hass.localize("ui.dialogs.more_info_control.show_more");
        case "area":
            return hass.localize("ui.panel.lovelace.editor.card.area.name");
        case "floor":
            return hass.localize("ui.components.selectors.selector.types.floor");
        case "icon":
            return hass.localize("ui.components.selectors.selector.types.icon");
        case "entity":
            return hass.localize("ui.components.selectors.selector.types.entity");
        case "color":
            return hass.localize("ui.panel.lovelace.editor.card.tile.color");
        case "label":
            return hass.localize("ui.components.label-picker.label");
        case "sortOrder":
            return "Order";
        case "moreInfoColumns":
            return hass.localize("ui.panel.lovelace.editor.action-editor.actions.more-info") + " " + hass.localize("ui.panel.lovelace.editor.card.grid.columns");
        case "extra_entities":
            return "Extra" + " " + hass.localize("ui.panel.lovelace.editor.card.entities.name");
        case "edit_filters":
            return hass.localize(`ui.panel.lovelace.editor.common.edit`) + " " + hass.localize(`ui.components.subpage-data-table.filters`);
        case "labelFilter":
            return hass.localize("ui.components.label-picker.label") + " " + hass.localize("ui.components.related-filter-menu.filter");
        case "hideDomain":
            return hass.localize(`component.${domain}.entity_component._.name`) + " " + hass.localize("ui.common.disable");
        case "invertDomain":
            return hass.localize(`component.${domain}.entity_component._.name`) + " " + hass.localize("ui.dialogs.entity_registry.editor.invert.label");
        case "hide_cover_DeviceClass":
            return hass.localize(`ui.dialogs.entity_registry.editor.device_classes.cover.${deviceClass}`) + " " + hass.localize("ui.common.disable");
        case "hide_binary_sensor_DeviceClass":
            return hass.localize(`ui.dialogs.entity_registry.editor.device_classes.binary_sensor.${deviceClass}`) + " " + hass.localize("ui.common.disable");
        case "invert_cover_DeviceClass":
            return hass.localize(`ui.dialogs.entity_registry.editor.device_classes.cover.${deviceClass}`) + " " + hass.localize("ui.dialogs.entity_registry.editor.invert.label");
        case "invert_binary_sensor_DeviceClass":
            return hass.localize(`ui.dialogs.entity_registry.editor.device_classes.binary_sensor.${deviceClass}`) + " " + hass.localize("ui.dialogs.entity_registry.editor.invert.label");
        case "showPersonName":
            return hass.localize("component.person.entity_component._.name") + " " + hass.localize("ui.panel.lovelace.editor.card.generic.show_name");
        case "showBadgeName":
            return hass.localize("ui.panel.lovelace.editor.cardpicker.domain") + " " + hass.localize("ui.panel.lovelace.editor.card.generic.show_name");
        case "showPerson":
            return translateState('show_person', hass.language);
        case "bulkMode":
            return translateState('bulk_mode', hass.language);
        case "edit_domains_dc":
            return translateState('edit_domains_dc', hass.language);
        case "edit_domains":
            return translateState('edit_domains', hass.language);
        case "edit_cover_dc":
            return translateState('edit_cover_dc', hass.language);
        case "edit_binary_sensor_dc":
            return translateState('edit_binary_sensor_dc', hass.language);
        case "domainName":
            return hass.localize("ui.panel.lovelace.editor.cardpicker.domain");
        case "deviceClassName":
            return "Device Class";
        default:
            if (ALLOWED_DOMAINS.includes(schema.name)) {
                return hass.localize(`component.${schema.name}.entity_component._.name`) || schema.name;
            }
            for (const [domain, classes] of Object.entries(deviceClasses)) {
                if (classes.includes(schema.name)) {
                    return (hass.localize(`ui.dialogs.entity_registry.editor.device_classes.${domain}.${schema.name}`) || schema.name);
                }
            }
            return hass.localize(`ui.panel.lovelace.editor.card.area.${schema.name}`);
    }
}
